import React, { useState } from 'react';

export const AddTodo = ({addTodo}) => {
    const [title, setTitle] = useState("");
    const [desc, setDesc] = useState("");


    const submit = (e)=>{
        e.preventDefault();
        if(!title || !desc){
          alert("title on description cannot be black")
        }
        else{
          
          addTodo(title,desc);
          setTitle("");
          setDesc("");
        }

      }
      return (
        <div className="container my-3">
          <h3>Add a Todo</h3>
        <form onSubmit={submit}>
        <h3 className='text-center'>Add Todo List </h3>
    <div className="mb-3 ">
      <label for="title" className="form-label">Todos Title</label>
      <input type="text" value={title} onChange={(e)=>setTitle(e.target.value)} className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"/>
      <div id="emailHelp" className="form-text"></div>
    </div>
    <div className="mb-3">
      <label for="desc"  className="form-label">Todos Descriptions</label>
      <input type="text" value={desc} onChange={(e)=>setDesc(e.target.value)} className="form-control" id="exampleInputPassword1"/>
    </div>
    <button type="submit" className="btn btn-sm btn-success">Add Todo</button>
    
  </form>
        </div>
)
}


